<template>
    <div>
        <div class="container">
            <cube-sticky
                :pos="scrollY"
                :check-top="checkTop"
                fixed-show-ani="sticky-fixed-show"
                @diff-change="diffChange"
            >
                <cube-scroll :scroll-events="scrollEvents" @scroll="scrollHandler">
                    <div class="header">
                        <div class="back" @click="back">
                            <i class="icon-arrow icon-arrow-l"></i>
                        </div>
                        <div class="title">书籍详情</div>
                        <div class="search"></div>
                    </div>
                    <!-- cube-sticky-ele元素配合check-top使用，控制吸附条件 -->
                    <cube-sticky-ele>
                        <div class="sticky-header">书籍详情</div>
                    </cube-sticky-ele>

                    <div class="details">
                        <img class="bg" :src="bookDetail.book_cover" />
                        <div class="content">
                            <img :src="bookDetail.book_cover" />
                            <div class="text">
                                <h3>{{bookDetail.book_name}}</h3>
                                <h4>{{bookDetail.author_name}}</h4>
                                <div>
                                    <span>{{bookDetail.book_is_action? '已完结': '连载中'}}</span>
                                    <em>|</em>
                                    <span>{{bookDetail.category_name}}</span>
                                    <em>|</em>
                                    <span>{{bookDetail.book_word_num}}</span>
                                </div>
                                <p>{{bookDetail.book_read_num}}人阅读</p>
                            </div>
                        </div>
                        <div @click="changeOpenState()" :class="['brief', isOpenState? '': 'hide']">
                            简介：{{bookDetail.book_brief}}
                            <div class="icon">
                                <span
                                    :class="['icon-arrow', isOpenState? 'icon-arrow-t': 'icon-arrow-b']"
                                ></span>
                            </div>
                        </div>
                    </div>

                    <div class="readbtn">
                        <ul class="read">
                            <li>加入书架</li>
                            <li @click="goRead(bookId, bookDetail.chapter_new_id)">立即阅读</li>
                            <!-- <li>
                                <router-link :to="'/read/'+bookId+'/'+bookDetail.chapter_new_id">立即阅读</router-link>
                            </li>-->
                        </ul>
                        <div class="download">下载小说App · 免费畅读更多精彩内容</div>
                        <div class="chapter" @click="goCatalog(bookId)">
                            <div
                                class="chapter-name"
                            >{{bookDetail.chapter_new_name}}{{bookDetail.chapter_new_name}}</div>
                            <div class="chapter-show">
                                查看目录
                                <i class="icon-arrow"></i>
                            </div>
                        </div>
                    </div>
                    <hori-four
                        :moduleName="'猜你喜欢'"
                        :bookList="recommendList"
                        :moduleType="1"
                        @refresh="refresh"
                    ></hori-four>
                    <hori-four
                        :moduleName="'大家都在看'"
                        :bookList="allReadList"
                        :moduleType="2"
                        @refresh="refresh"
                    ></hori-four>
                </cube-scroll>
                <!-- 滑动吸附顶部 -->
                <div class="header sticky-header" slot="fixed" ref="stickyHeader">
                    <div class="back" @click="back">
                        <i class="icon-arrow icon-arrow-l"></i>
                    </div>
                    <div class="title">{{bookDetail.book_name}}</div>
                    <div class="search"></div>
                </div>
                <!-- <ul class="sticky-header" slot="fixed" ref="stickyHeader">
                    <li>header</li>
                </ul>-->
            </cube-sticky>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
import HoriFour from './hori-four.vue'
export default {
    name: 'Details',
    data() {
        return {
            scrollEvents: ['scroll'],
            scrollY: 0,
            checkTop: false,
            isOpenState: false,
            bookId: 0,
            bookFromId: 0,
            categoryId: 0,
            bookDetail: {},
            recommendList: [],
            allReadList: []
        }
    },
    created() {
        this.bookId = this.$route.params.id
        this.getNovelDetail()
    },
    methods: {
        scrollHandler({ y }) {
            this.scrollY = -y
        },
        diffChange(diff, height) {
            let opacity = 0
            if (height) {
                opacity = diff / height
            }
            this.$refs.stickyHeader.style.opacity = opacity
        },
        getNovelDetail() {
            let data = {
                nid: this.bookId
            }
            this.$post('/novel/detail', data).then(res => {
                // console.log(res)
                if (res.code === 10000) {
                    this.bookDetail = res.result
                    this.bookDetail.book_read_num = this.formatNumber(
                        this.bookDetail.book_read_num
                    )
                    this.bookDetail.book_word_num = this.formatNumber(
                        this.bookDetail.book_word_num
                    )
                    this.bookFromId = res.result.book_from_id
                    this.categoryId = res.result.category_id
                    this.getNovelRecommend()
                    this.getTypeChange()
                }
            })
        },
        /**
         * 猜你喜欢
         */
        getNovelRecommend() {
            let data = {
                nid: this.bookId,
                tid: this.categoryId
            }
            this.$post('/novel/recommend', data).then(res => {
                // console.log(res)
                if (res.code === 10000) {
                    this.recommendList = res.result
                }
            })
        },
        /**
         * 大家都在看
         */
        getTypeChange() {
            let data = {
                type_id: 11,
                pn: 1,
                count: 3
            }
            this.$post('/book/types_change', data).then(res => {
                // console.log(res)
                if (res.code === 10000) {
                    this.allReadList = res.result
                }
            })
        },
        formatNumber(num) {
            return num > 10000 ? (num / 10000).toFixed(2) + '万' : num
        },
        changeOpenState() {
            this.isOpenState = !this.isOpenState
        },
        refresh(num) {
            if (num == 2) {
                this.getTypeChange()
            } else {
                this.getNovelRecommend()
            }
        },
        back() {
            this.$router.back()
        },
        goCatalog(id) {
            this.$router.push({
                path: `/catalog/${id}`
            })
        },
        goRead(id, chapter) {
            console.log('阅读')
            this.$router.push({
                path: `/read/${id}/${chapter}`
            })
        }
    },
    components: {
        HoriFour
    },
    watch: {
        $route(to, from) {
            //监听路由是否变化
            if (to.params.id != from.params.id) {
                this.bookId = to.params.id
                this.getNovelDetail()
            }
        }
    }
}
</script>
