<template>
    <div class="module" v-if="bookList.length">
        <div class="title">
            {{moduleName}}
        </div>
        <div class="book-list">
            <a  
                class="book-item"
                v-for="book in bookList"
                :key="book.book_id"
                href="javascript:;"
                @click="goBook(book.book_id)">
                <div class="cover">
                    <img :src="book.book_cover">    
                </div>    
                <div class="text">
                    {{book.book_name}}
                </div>
            </a>
        </div>
        <div class="more" @click="goRefresh(moduleType)">
            <span>换一换</span>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
export default {
    name: 'HoriThree',
    props: {
        bookList: {
            type: Array,
            default: function(){
                return []
            }
        },
        moduleName: {
            type: String,
            default: ''
        },
        moduleType: {
            type: Number,
            default: 1
        }
    },
    methods: {
        goBook(id){
            this.$router.push({
                path: `/book/${id}`
            })
            this.$forceUpdate()
        },
        goRefresh(num) {
            this.$emit('refresh', num)
        }
    }
    
}
</script>
